*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: system-ui, 
    -apple-system, BlinkMacSystemFont, 
    'Segoe UI', Roboto, Oxygen, Ubuntu, 
    Cantarell, 'Open Sans', 'Helvetica Neue',
     sans-serif;
    background: rgb(214, 203, 203);
    color: #222;
}

.bookList{
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}

@media screen and (min-width:768px){
    .bookList{
        grid-template-columns: repeat(3,1fr);
        align-items: start;
    }

}

.book{
    background: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    position: relative;
}
.book img{
    width: 70%;
    object-fit: cover;
}

.book h2{
    margin-top: 1rem;
    font-size: 1rem;
}

.book h4 {
    color: #617d98;
    font-size: 0.74rem;
    margin-top: 0.5rem;
    letter-spacing: 2px;
}

.book p{
    margin: 1rem 0 0.5rem;
}

.number{
    position: absolute;
    top:0;
    left:0;
    padding: 0.75rem;
    font-size: 1rem;
    border-top-left-radius:1rem ;
    border-bottom-right-radius: 1rem;
    color: #fff;
    background-color: #c35600;
}

.pageTitle{
    text-align: center;
    margin-top: 4rem;
    text-transform: capitalize;

}

